import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import axios from 'axios';
import _ from 'lodash';
import './index.scss';

class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            links: [],
            list: []
        }
    }

    componentDidMount() {
        axios.get('/api/contacts').then(res => {
            let links = _.get(res, 'data', {})
            this.setState({ links })
        })
        axios.get('/api/articles/list_title').then(res => {
            let list = _.get(res, 'data', {})
            this.setState({ list })
        })
    }

    render() {
        let { links, list } = this.state
        return (
            <div className="lh-list">
                <div className="header">LIHAO</div>
                <p className="links">
                    {links.map(({ iconUrl, url }) => (
                        <a href={url} target="_blank" ref="norefer">
                            <img src={iconUrl} />
                        </a>
                    ))}
                </p>
                <div className="main">
                    <ul>
                        {
                            list.map(({ createDate, title, id }) => (
                                <li>
                                    <h3>{moment(createDate).format('MMM DD, YYYY')}</h3>
                                    <h2>
                                        <Link to={`/${id}`}>{title}</Link>
                                    </h2>
                                </li>
                            ))
                        }
                    </ul>
                </div>
            </div>
        )
    }
}

export default List;