import React, { Component } from 'react';
import moment from 'moment';
import { Converter } from 'showdown';
import showdownHighlight from 'showdown-highlight'
import { Link } from 'react-router-dom';
import axios from 'axios';
import _ from 'lodash';
// import './index.css';
// import './theme.scss';

const converter = new Converter({
    extensions: [showdownHighlight],
    tables: false
});

class Detail extends Component {
    constructor(props) {
        super(props)
        this.state = {
            links: [],
            createDate: "",
            title: "",
            content: ""
        }
    }

    componentDidMount() {
        axios.get(`/api/articles/${_.get(this.props, 'match.params.id')}`).then(res => {
            let data = _.get(res, 'data', {})
            this.setState(data)
        })
        axios.get('/api/contacts').then(res => {
            let links = _.get(res, 'data', {})
            this.setState({ links })
        })
    }

    render() {
        let { links, createDate, title, content } = this.state
        return (
            <div className="detail">
                <div className="header"><Link to="/">LIHAO</Link></div>
                <p className="links">
                    {links.map(({ iconUrl, url }) => (
                        <a href={url} target="_blank" ref="norefer">
                            <img src={iconUrl} />
                        </a>
                    ))}
                </p>
                <div className="main" style={{ maxWidth: 800 }}>
                    <h3 className="date">{moment(createDate).format('MMM DD, YYYY')}</h3>
                    <h1>{title}</h1>
                    <div className="markdown-body" dangerouslySetInnerHTML={{ __html: converter.makeHtml(content) }}></div>
                </div>
            </div>
        )
    }
}

export default Detail; 